@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Comfortaa&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Comfortaa&display=swap");
.App {
  width: 100%;
  height: 100vh;
  background: linear-gradient(81deg, #11466f, #2041c5, #0099ff);
  font-family: "Dancing Script", cursive;
}
.App .wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App .wrapper .quizeBord {
  background-color: azure;
  display: flex;
  max-width: 900px;
  justify-content: space-around;
  flex-direction: column;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
  padding: 15px;
  border-radius: 30px;
}
.App .wrapper .quizeBord .mainScreen {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.App .wrapper .quizeBord .mainScreen .mainScreenText h1 {
  font-weight: 600;
}
.App .wrapper .quizeBord .mainScreen .mainScreenText p {
  margin-top: 10px;
  font-size: 18px;
  width: 550px;
  font-weight: normal;
  font-family: "Comfortaa", cursive;
}
.App .wrapper .quizeBord .mainScreen .mainScreenBtn {
  display: flex;
  justify-content: space-between;
  margin: 40px 0px 10px;
  width: 100%;
}
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnPlay,
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnNewQuestion {
  display: flex;
  flex-direction: column;
  border: none;
}
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnPlay button,
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnNewQuestion button {
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.8);
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 18px;
  font-family: "Comfortaa", cursive;
  border: none;
  color: white;
  background: linear-gradient(49deg, #4553bf, #4f7873);
  opacity: 0.8;
}
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnPlay button:hover,
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnNewQuestion button:hover {
  background: linear-gradient(49deg, #4553bf, #4f7873);
  transition: 0.2s;
  opacity: 1;
}
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnPlay button:active,
.App .wrapper .quizeBord .mainScreen .mainScreenBtn .btnNewQuestion button:active {
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.8);
}

.quizeTitle {
  font-size: 30px;
}

.quize {
  font-size: 20px;
}

.variants {
  font-family: "Comfortaa", cursive;
  cursor: pointer;
  padding: 10px 15px;
  border: 2px solid grey;
  width: 80%;
  margin: 10px auto 10px;
  border-radius: 20px;
  list-style-type: none;
  transition: 0.2s;
}
.variants:hover {
  border: 2px solid #008ae6;
  background: #99d6ff;
}
.variants:active {
  background: #006bb3;
}

.gameTitle {
  text-align: center;
}

.gameCorrect {
  text-align: center;
}

.gameBtn {
  border: none;
  display: flex;
  justify-content: space-around;
  padding: 10px 20px;
}
.gameBtn .newGame,
.gameBtn .reverse {
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 18px;
  font-family: "Comfortaa", cursive;
  border: none;
  color: white;
  background: linear-gradient(49deg, #4553bf, #4f7873);
  opacity: 0.8;
  margin: 5px;
}
.gameBtn .newGame:hover,
.gameBtn .reverse:hover {
  background: linear-gradient(49deg, #4553bf, #4f7873);
  transition: 0.2s;
  opacity: 1;
}
.gameBtn .newGame:active,
.gameBtn .reverse:active {
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.8);
}

.progress {
  font: 12px Arial, Tahoma, sans-serif;
  position: relative;
  overflow: hidden;
}

.progress progress {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  left: -777px;
}

.progress-bar {
  overflow: hidden;
  background: #3366ff;
  border-radius: 10px;
  width: 0;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.progress-bg {
  background: #e6e9ed;
  position: relative;
  height: 8px;
  border-radius: 5px;
  overflow: hidden;
}

.progress-bar:after {
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
  background-size: 40px 40px;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@keyframes progress_bar {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -40px 0;
  }
}
.progress-bar {
  transition: width 1s linear;
}

.progress-bar:after {
  animation: progress_bar 0.8s linear infinite;
}

.progress progress[value="1"] + .progress-value:before {
  content: "1%";
}

.progress progress[value="1"] ~ .progress-bg .progress-bar {
  width: 1%;
