.titleAnswer,
.correctTitle,
.question {
  margin-top: 10px;
  font-weight: 700;
}

.titleAnswer,
.correctTitle,
.question {
  font-size: 20px;
}

.correctTitle {
  margin-top: 30px;
}

.PreparationAnswers,
.PreparationQuestion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.PreparationAnswers input[type=text],
.PreparationQuestion input[type=text] {
  margin-top: 5px;
  border: none;
  border-radius: 10px;
  min-width: 250px;
  padding: 10px 5px;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.7);
  font-size: 18px;
}
.PreparationAnswers input::-moz-placeholder, .PreparationQuestion input::-moz-placeholder {
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
  font-size: 15px;
}
.PreparationAnswers input::placeholder,
.PreparationQuestion input::placeholder {
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
  font-size: 15px;
}

.PreparationBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 30px 0px 20px;
}
.PreparationBtn .startQuiz,
.PreparationBtn .addQuestion {
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 18px;
  font-family: "Comfortaa", cursive;
  border: none;
  color: white;
  background: linear-gradient(49deg, #4553bf, #4f7873);
  opacity: 0.8;
  margin: 5px;
}
.PreparationBtn .startQuiz:hover,
.PreparationBtn .addQuestion:hover {
  background: linear-gradient(49deg, #4553bf, #4f7873);
  transition: 0.2s;
  opacity: 1;
}
.PreparationBtn .startQuiz:active,
.PreparationBtn .addQuestion:active {
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.8);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}/*# sourceMappingURL=main.css.map */